<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            DEPARTMENT LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-row class="mt-2">
        <v-col cols="12" md="12">
          <v-card>
            <div class="pl-5 pr-5">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-card-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          color="primary"
                          to="/addDept"
                          class="text-decoration-none"
                          dark
                          fab
                          v-bind="attrs"
                          v-on="on"
                          small
                        >
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>ADD DEPARTMENT</span>
                    </v-tooltip>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>

                  <v-data-table
                    :headers="headers"
                    :items="depts"
                    :search="search"
                    :hide-default-footer="false"
                  >
                    <template slot="item.numbering" slot-scope="props">
                      {{ props.index + 1 }}
                    </template>

                    <!-- <template v-slot:[`item.status`]="{ item }">
                      <v-chip :color="getStatusColor(item.status)" dark small>
                        <tr>
                          {{
                            item.status.toLowerCase()
                          }}
                        </tr>
                      </v-chip>
                    </template>

                    <template v-slot:[`item.roles`]="{ item }">
                      <v-chip :color="getRolesColor(item.roles)" dark small>
                        <tr>
                          {{
                            item.roles[0].name.toLowerCase()
                          }}
                        </tr>
                      </v-chip>
                    </template> -->

                    <template v-slot:[`item.actions`]="{ item }">
                      <v-chip
                        dark
                        small
                        color="teal lighten-2"
                        @click="editDept(item.id)"
                        class="mr-1"
                      >
                        <span>EDIT</span>
                      </v-chip>
                      <v-chip
                        dark
                        small
                        color="red darken-1"
                        @click="deleteDept(item.id)"
                      >
                        <span>DELETE</span>
                      </v-chip>
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import DeptDataService from "../../services/DeptDataService";
export default {
  name: "dept-list",
  data() {
    return {
      depts: [],
      search: "",
      headers: [
        {
          text: "NO.",
          align: "start",
          sortable: false,
          value: "numbering",
          // class: "blue lighten-5",
        },
        {
          text: "NAME",
          align: "start",
          sortable: false,
          value: "name",
          // class: "blue lighten-5",
        },
        {
          text: "DESCRIPTION",
          align: "center",
          value: "Description",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "ACTIONS",
          align: "center",
          value: "actions",
          sortable: false,
          // class: "blue lighten-5",
        },
      ],
    };
  },

  computed: {},

  methods: {
    retrieveDepts() {
      DeptDataService.getAll()
        .then((response) => {
          this.depts = response.data.map(this.getDisplayDept);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveDepts();
    },

    // getRolesColor(roles) {
    //   if (roles[0].name == "ADMIN") return "red";
    //   else if (roles[0].name == "MANAGER") return "orange";
    //   else if (roles[0].name == "BRANCH MANAGER") return "indigo";
    //   else return "grey";
    // },

    // getStatusColor(status) {
    //   if (status == "Inactive") return "green lighten-2";
    //   else return "green darken-3";
    // },

    removeAllDepts() {
      DeptDataService.deleteAll()
        .then((response) => {
          console.log(response.data);
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    // searchName() {
    //   DeptDataService.findByName(this.name)
    //     .then((response) => {
    //       this.depts = response.data.map(this.getDisplayDept);
    //       console.log(response.data);
    //     })
    //     .catch((e) => {
    //       console.log(e);
    //     });
    // },

    editDept(id) {
      this.$router.push({ name: "dept", params: { id: id } });
    },

    deleteDept(id) {
      DeptDataService.delete(id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayDept(dept) {
      return {
        id: dept.id,
        name:
          dept.name.length > 30 ? dept.name.substr(0, 30) + "..." : dept.name,
        Description:
          dept.description.length > 30
            ? dept.description.substr(0, 30) + "..."
            : dept.description,
      };
    },
  },
  mounted() {
    this.retrieveDepts();
  },
};
</script>
